/**
 * EndowmentDetailContainer.js
 * Detail Container for Endowment
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import EndowmentDetail from '../../components/Endowment/EndowmentDetail';
import { getAnonymousSession, setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { getSessionParams } from '../../actions/endowmentActions';
import { fbTag, gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class EndowmentDetailContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.DETAILFORM.index);
    fbTag('PPS2 - Pageview - Endowment - DetailForm', { Product: 'Endowment' });
    gaTag({
      page_title: 'Endowment - Detail Form',
      page_path: '/endowment/detailform',
    });
    ampTag('EndDetailsS');
  }

  render() {
    const {
      plansData,
      isLoggedIn,
      profileValues,
      piggyCallback,
      endowmentFields,
      sessionId,
      checkOutOthers,
      history,
    } = this.props;

    if (!plansData) {
      return null;
    }

    const selectedData = plansData;

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      let netPrice = selectedData.policyInfo[selectedData.planType].net_price;
      netPrice = netPrice ? `$${netPrice.toFixed(2)}` : '';

      planDetails = {
        logo: Logos[selectedData.info.insurer_slug],
        logoDesc: `${selectedData.info.insurer_name}.svg`,
        planName: selectedData.info.product_name,
        planTotal: netPrice,
      };
    } catch (error) {
      console.log('Get plan details failed');
    }

    return (
      <EndowmentDetail
        endowmentFields={endowmentFields}
        selectedData={selectedData}
        planDetails={planDetails}
        isLoggedIn={isLoggedIn}
        sessionId={sessionId}
        piggyCallback={piggyCallback}
        checkOutOthers={checkOutOthers}
        changePlan={() => {
          history.goBack();
          fbTag('PPS2 - ButtonClick - Endowment - DetailForm_ChangePlan', { Product: 'Endowment', Click_Text: 'ChangePlan' });
          gaEvent('Endowment - DetailForm - ChangePlan', {
            event_category: 'Endowment',
            event_label: 'Endowment - DetailForm - ChangePlan',
          });
        }}
        profileValues={profileValues}
        // submitCallback={() => { setFormState(FORMSTATES.CHECKOUT); }}
      />
    );
  }
}

EndowmentDetailContainer.defaultProps = {
  sessionId: null,
  plansData: null,
};

EndowmentDetailContainer.propTypes = {
  plansData: PropTypes.object,
  piggyCallback: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  setFormState: PropTypes.func.isRequired,
  profileValues: PropTypes.object.isRequired,
  endowmentFields: PropTypes.object.isRequired,
  sessionId: PropTypes.string,
  checkOutOthers: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};


const mapStateToProps = state => ({
  endowmentForm: getFormValues('endowment')(state),
  endowmentFields: state.endowment.endowmentFields,
  profileValues: state.userDetails,
  plansData: state.endowment.selectedPlanData,
  sessionId: state.insurance.sessionId,
});

const mapDispatchToProps = dispatch => ({
  getAnonymousSession: (params) => {
    dispatch(getAnonymousSession(params, true));
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

const mergeProps = (propsFromState, propsFromDispatch, ownProps) => ({
  ...ownProps,
  ...propsFromState,
  ...propsFromDispatch,
  checkOutOthers: (detailForm) => {
    propsFromDispatch.getAnonymousSession(getSessionParams(
      propsFromState.endowmentForm,
      detailForm,
      propsFromState.plansData,
    ), true);
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(EndowmentDetailContainer));
